import React from "react";

export default () => {
  return (
    <div>
      <h1 className="display-4">
        <span className="text-danger">404</span> Page not Found
      </h1>
      <p className="lead">Sorry, that page, like the spoon, does not exist</p>
    </div>
  );
};



